import React from "react";
import {
  Jumbotron
} from "reactstrap";
import BootstrapTable from "react-bootstrap-table-next";

export class User extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loggedIn: true,
      isAdmin:true,
      currentState: "not-panic",
      items: []
    };
    }  


  componentDidMount() {
    fetch('/items', {
      headers : { 
        accept: 'application/json'

       }

    }) 
      .then(res => res.json())
      .then(items => this.setState({ items }));
  }

  render() {
    let columns = [
      {
        dataField: "id",
        text: "User ID"
      },
      {
        dataField: "name",
        text: "User Name",
      },
      {
        dataField: "price",
        text: "User Type"
      }
    ];

    return (
      <div>
        <Jumbotron>
          <h1 className="display-4">Users</h1>
          <br/>
          <BootstrapTable
            keyField="id"
            data={items}
            columns={columns}
            striped
            hover
            condensed
          />
        </Jumbotron>
        <h1>Users</h1>
        {this.state.items.map(item =>
          <div key={item.id}>{item.name}</div>
        )}
      </div>
    );
  }
}



